import React, { useLayoutEffect } from "react";
import useArticle from "../hooks/useArticle";
import disqus from "../etcs/disqus";

function Article({ match }: any) {
  const { articletState, fetchArticle } = useArticle();
  useLayoutEffect(() => {
    // DON'T EDIT BELOW THIS LINE
    
    let nowPage:boolean = true;
    disqus();

    if (nowPage) {
      fetchArticle(match.params.page, match.params.id);
      nowPage = false
      console.log(articletState);
      
    }
    
    return () => {};
  });
  return (
    <>
      <h1>
        <span id="title"></span>
      </h1>
      <h3>
        <i className="fas fa-user-alt"></i> 삼피
        <i className="far fa-calendar-alt"></i> <span id="date"></span>
      </h3>
      <hr />
      <div id="content"></div>
      <hr />
      <div id="disqus_thread"></div>
    </>
  );
}

export default Article;
